.preview-container .arrow::after {
  display: block;
  z-index: 20;
  content: '';

  position: absolute;
  left: 20px;
  bottom: 2px;

  width: 0;
  height: 0;

  border-left: 20px solid transparent;
  border-top: 10px solid transparent;
  border-right: 20px solid transparent;
  border-bottom: 10px solid transparent;
  
  border-left-color: black;
  border-right: 0;

  -webkit-transform: translateY(50%);

          transform: translateY(50%);
}

.preview-container .arrow::before {
  display: block;
  z-index: 20;
  content: '';

  position: absolute;
  left: -8px;
  bottom: 20px;

  width: 0;
  height: 0;

  border-left: 20px solid transparent;
  border-top: 10px solid transparent;
  border-right: 20px solid transparent;
  border-bottom: 10px solid transparent;
  
  border-left-color: black;
  border-right: 0;

  -webkit-transform: rotate(-90deg);

          transform: rotate(-90deg);
}
.preview-container .window1 {
  grid-area: window1;
  position: relative;
  background-color: darkgray;
}

.preview-container .window2 {
  grid-area: window2;
  background-color: darkgray;
}

.window1 .topArrow{
  position: absolute;
  left: 0;
  bottom: 0;
  height: 20px;
  width: 5px;
  background-color: black;
}

.window1 .rightArrow{
  position: absolute;
