 .number-input {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    display: block;
    height: 26px;
    width: 46px;
    margin: 0 6px 0 4px;
    border: 1px solid lightgray;
 }

 .number-input div {
     display: inline-block;
 }
 
 .number-input .val {
    width: 20px;
    text-align: center;
}

 .number-input .ctl {
    background-color: lightgray;
    width: 12px;
    text-align: center;
    cursor: pointer;
 }

 .number-input .ctl:hover {
    background-color: gray;
 }
/* SizeOptionsPanel style */
.container-window-options {
  position: relative;
  display: grid;
  grid-template-columns: 65px 720px 65px;
  grid-template-rows: auto 600px auto;
  grid-template-areas:
    ". topSide tools"
    "leftSide preview rightSide"
    ". bottomSide .";
    justify-items: center;
    align-items: center;
  
  margin: 0 auto;
  margin-bottom: 5px;
}

/* Side options style */

.side-options-container {
  display: flex;
}

.side-options-container-item {
  margin-right: 3px;
}

.side-options-container-item input,
.side-options-container-item label  {
  padding: 0;
  margin: 0;
  font-size: 14px;
  text-align: center;
  width: 40px;
  height: 20px;
}
.texture-panel-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

/* .texture-panel-container .texture-panel-container-item {
  margin-right: 5px;
} */

.texture-panel-container .texture-name {
  width: 250px;
  height: 60px;

  display: flex;
  align-items: center;
  overflow: hidden;
}

.texture-panel-container .custom-file {
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 40px;
  height: 40px;

  opacity: 0;
}

.texture-panel-container .custom-file-input {
  display: block;
  width: 0;
  height: 0;
}

