.texture-panel-container .imgPreview {
    position: relative;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 120px;
    width: 120px;
    background-color: gray;
    border: 2px solid gray;
    cursor: pointer;
    background-size: contain;
}

.texture-panel-container .imgPreview img {
  flex-shrink: 0;
  -webkit-flex-shrink: 0;
  max-width: 70%;
  max-height: 90%;
}

.texture-panel-container .centerText {
  text-align: center;
  width: 500px;
}

.texture-panel-container .preview-list {
  position: absolute;
  top: 100%;
  right: 0;
  z-index: 21;

  width: 300px;
  height: 300px;

  overflow-y: scroll;
  overflow-x: auto;
  border: 2px solid gray;
  background-color: lightgray;
}

.add-texture-button {
  width: 100%;
  height: 30px;
  border: 2px solid black;
  cursor: pointer;
  background: white;
}

.add-texture-button:hover {
  background-color: lightgray;
}

.texture-panel-container .preview-list .preview-history-wrapper {
  position: relative;
  height: 60px;

  margin-top: -1px;
  margin-left: 2px;
}

.texture-panel-container .preview-list .preview-history {
  /* position: fixed; */
  width: 278px;
  height: 60px;
  background-color: white;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.texture-panel-container .preview-list .preview-history .preview-history-item {
  display: inline-block;
  width: 50px;
  height: 50px;
  margin-left: 3px;
  margin-right: 3px;
}

.texture-panel-container
  .preview-list
  .preview-history
  .preview-history-item:hover {
  border: 2px solid gray;
}

.texture-panel-container
  .preview-list
  .preview-history
  .preview-history-item
  img {
  width: 100%;
  height: 100%;
}

.texture-panel-container .preview-list p {
  max-width: 200px;
  white-space: nowrap;
  display: inline-block;
  line-height: 2;
  margin-left: 5px;
}

.texture-panel-container .preview-list .preview-list-item {
  display: inline-block;
  margin-left: 5px;
  display: flex;
  justify-content: flex-start;
}

.texture-panel-container .preview-list .preview-list-item:first-child {
  margin-top: 60px;
}

.texture-panel-container .preview-list .preview-list-item:hover {
  background-color: whitesmoke;
}

.texture-panel-container .preview-list .preview-list-item .img {
  background-size: contain;
  display: inline-block;
  padding: 1px;
  margin: 1px;
  width: 30px;
  height: 30px;
}

