p,
img {
  margin: 0;
  padding: 0;
}

/* ColorInsertEditor styles */

.app-container {
  width: 850px;
  margin: 0 auto;
  padding: 0;
}

.app-container-border {
  border: 2px solid gray;
}

.app-container .container-item {
  width: 520px;
  margin-left: 60px;
}
/* Window Size */

.window-size-container {
  display: flex;
  justify-content: space-around;
}

.app-container .type-toggle {
  display: flex;
  justify-content: flex-start;
  margin-left: 43px;
}

.app-container .type-toggle p {
  margin: 0;
  margin-right: 10px;
  padding: 0;

  line-height: 38px;
}

.app-container .type-toggle .texture-type-toggle {
  margin-left: 123px;
}

.container-control-buttons {
  margin-left: -180px;
  margin-bottom: 3px;
  display: flex;
}

.container-control-buttons .basket-icon {
  padding-left: 3px;
  border-left: 2px solid gray;
}

.icon {
  position: relative;
  display: block;
  width: 35px;
  height: 35px;
  margin-right: 2px;
  padding: 0;
}

.icon img {
  width: 100%;
  height: 100%;
}

.window-icon {
  position: relative;
  display: block;
  width: 33px;
  height: 35px;
  box-sizing: border-box;
  border: 4px solid black;
  border-radius: 15%;
  margin-right: 10px;
}

.double-window::after {
  position: absolute;
  display: block;
  content: "";

  width: 4px;
  height: 100%;
  background-color: black;

  top: 0;
  right: 30%;
}

/*  save-panel styles  */

.app-container .save-panel {
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-top: 2px;
  margin-bottom: 10px;
}

.app-container .save-panel p {
  padding: 0;
  margin: 0;
}

.app-container .save-panel input {
  max-width: 300px;
}

/* footer styles */

.footer-button {
  width: 720px;
  margin-left: 63px;
  margin-top: 15px;
}

.footer-button .btn {
  width: 360px;
  margin: 2px;
}


.tools-panel {
  position: relative;
  left: 67px;
  width: 720px;
  margin-top: 14px;
  margin-bottom: 14px;
  display: grid;
  grid-template-columns: 363px 237px 120px;
  grid-template-rows: 23px 40px 40px 40px;
  grid-template-areas: "a1 b1 c1"
                       "a2 b2 c2"
                       "a3 b3 c3"
                       "a4 b4 c4";
}

.tools-panel .otdelka-type {
  position: relative;
  top: 7px;
  grid-area: a3;
}

.tools-panel .texture-name {
  position: relative;
  grid-area: a1;
}

.tools-panel .root-type-toggle {
  grid-area: b4;  
}

.tools-panel .shift-settings {
  flex-direction: row;
  display: flex;
  grid-area: 'b2';
  position: relative;
  top: 5px;
  left: -3px;
}

.tools-panel .texture-name {
  grid-area: c1;
  text-align: center;
  overflow: hidden;
}